body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

.field {
  margin-bottom: 5px;
  padding: 8px;
  display: inline-block;
}

.field label {
  display: block;
}

.field input {
  padding: 4px;
}

.download {
  margin: 8px;
}

.result-container {
  padding: 16px;
  margin-bottom: 16px;
}

.result {
  width: 450px;
  height: 340px;
  background: red;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.result .img {
  position: relative;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.result .img img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.result .text {
  background: #f2f3f5;
  padding: 10px 12px;
  font-size: 16px;
  font-family: Helvetica;
  border: 1px solid #dadada;
  position: relative;
}

.result .text .close {
  position: absolute;
  background: url(data:image/png;base64,);
  width: 12px;
  height: 12px;
  background-position: 0 -144px;
  right: 8px;
}

.result .text .source {
  font-size: 12px;
  height: 11px;
  color: #606770;
  padding: 0;
  text-transform: uppercase;
}

.result .text .headline {
  font-weight: 600;
  color: #1d2129;
  line-height: 20px;
  margin-top: 5px;
}

.result .text .byline {
  font-size: 14px;
  word-break: break-word;
  white-space: nowrap;
  overflow-x: hidden;
  text-overflow: ellipsis;
  color: #606770;
  margin-top: 3px;
  padding-right: 8px;
}

